.databases{
    background: linear-gradient(280deg, rgb(5, 36, 82) 24%, rgb(3, 85, 137) 95%); 
   
}

.databases__intro{  
    padding: 70px 350px 100px;
    height: 100vh;
    }
    

    @media screen and (max-width: 1920px) {
        .databases__intro{  
            padding: 70px 150px 100px;           
            }
      }

      @media screen and (max-width: 1450px) {
        .databases__intro{  
            background-position: bottom right ;
            background-size: 70% auto;
            }
      }
      @media screen and (max-width: 1200px) {
        .databases__intro{  
            padding: 70px 100px 100px;           
            }
      }
      
     
      
      @media screen and (max-width:1000px) {
        .databases__intro{  
            padding: 70px 60px 60px;           
            }
      }
      
      @media screen and (max-width: 650px) {
        .databases__intro{  
            padding: 70px 18px 60px;           
            }
      }
    





    .databases__title{
        font-size: 86px;
        max-width: 560px;
        padding-top: 200px;
        padding-bottom: 30px; 
        
    }

    @media screen and (max-width: 1600px) {
        .databases__title{
          font-size: 50px;
          line-height: 58px; 
          max-width: 700px; 
        }
      }

    .databases__subtitle{
        font-size: 35px;
        line-height: 1.2;
        max-width: 700px;
    }

    @media screen and (max-width: 1600px) {
        .databases__subtitle{
            font-size: 26px;     
            max-width: 515px; 
          line-height: 32px; 
     
        }
      }


.databases__platform { 
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center; 
   
}




  .databases__platform_btn{
    cursor: pointer;
    outline: none;
    border: 0;   
    text-decoration: none;
    background-color: #12abdb;
    font-size: 20px;
    color: #fff;
    width: 380px;
    height: 50px;
    font-weight: 700;
    border-radius: 25px;
    margin-top: 50px;
  }

  .databases__platform_img {
    width: 36%;
    object-fit: contain;
    padding-right: 30px;
  }

  @media screen and (max-width: 950px) {
    .databases__platform_img{
      display: none;
   
    }
  }


.databases__about {
    background-color: hsl(201, 100%, 97%);
    padding-left: 350px;    
    padding-bottom: 180px;
    
}

@media screen and (max-width: 1920px) {
    .databases__about{
      padding-left: 150px;
    }
  }
  
  
  @media screen and (max-width: 1200px) {
    .databases__about {
      padding-left: 100px;
    }
  }
  
  
  
  @media screen and (max-width: 1000px) {
    .databases__about {
      padding-left: 60px;
    }
  }
  
  @media screen and (max-width: 650px) {
    .databases__about{
      padding-left: 18px;
    }
  }

.databases__about_title {
  color: #12abdb;
  font-size: 50px;
  line-height: 58px;
  margin: 0;
  font-weight: 700;
  max-width: 1000px;
  padding-bottom: 50px;
}

@media screen and (max-width: 1600px) {
    .databases__about_title{
      font-size: 40px;
      line-height: 48px; 
      max-width: 800px; 
      padding-bottom: 40px;
    }
  }

  @media screen and (max-width: 950px) {
    .databases__about_title{
      font-size: 30px;
      line-height: 38px; 
   
    }
  }

.databases__about_content {
  display: flex;  
  justify-content: space-between; 
  color: #000;
  font-weight: 500;
}

@media screen and (max-width: 950px) {
    .databases__about_content{
        flex-direction: column-reverse;
    }
  }

.databases__about_article {
    margin: 0;
    padding: 0;
    max-width: 900px;

    font-size: 24px;
    font-weight: 400;
    line-height: 32px;

    padding-right: 80px;
    padding-bottom: 40px;
}

@media screen and (max-width: 1600px) {
    .databases__about_article{
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
    }
  }

  @media screen and (max-width: 1400px) {
    .databases__about_article{
        padding-right: 60px;
    }
  }

  @media screen and (max-width: 1115px) {
    .databases__about_article{
        padding-right: 30px;
    }
  }

.databases__about_img {
    padding-top: 50px;
    width: 642px;
    padding-right: 100px;
    object-fit: contain;
}

@media screen and (max-width: 1600px) {
    .databases__about_img {
        padding-top: 0;
        width: 40%;
    }
  }

  
  @media screen and (max-width: 1400px) {
    .databases__about_img{
        padding-right: 60px;
    }
  }

  @media screen and (max-width: 1150px) {
    .databases__about_img{
        padding-right: 30px;
    }
  }

  @media screen and (max-width: 950px) {
    .databases__about_img{
        padding-bottom: 40px;
        width: 90%;
    }
  }
